import { configureStore, createSlice } from '@reduxjs/toolkit'


let cart = createSlice({
  name : 'cart',
  initialState : [
    {id : 0, name : 'White and Black', price : 120000, count : 2},
    {id : 2, name : 'Grey Yordan', price : 130000, count : 1}
  ],

  reducers : {

    increase(state, action){
      let num = state.findIndex(data => data.id === action.payload);
      state[num].count++
    },
    decrease(state, action){
      let num = state.findIndex(data => data.id === action.payload);
      state[num].count--
    },
    addCart(state, action){
      //버튼을 누르면 detail 페이지에 있는 result변수(array)의 내용중
      //id, title->name, price, count:1 를 가지고 오고 이미 id가 존재한다면 
      //count를 1늘리기 (버튼 dispatch에 추가할 array를 미리 탑재)
      //1. 버튼 누르기 -> 함수안의 array.id와 state.id가 동일한게 있는지 확인
      //2-1 있으면 해당 id가 있는 state.count를 1개 늘리기
      //2-2 없으면 state에 array unshift함
      // console.log(action.payload);
      
      let num = state.findIndex(data => data.id == action.payload.id);
      
      if (num == -1) {
        state.push(action.payload);
        // console.log(state);
       } else {
        state[num].count++;
        // console.log(state[num].count);
       }
      alert('장바구니에 추가되었습니다');
    },
    removeCart(state, action){
      let num = state.findIndex(data => data.id === action.payload);
      
      // console.log(num);
      state.splice(num, 1);
      alert('장바구니에서 삭제되었습니다');
    }
  }
});

export let { addCart, removeCart, increase, decrease } = cart.actions


export default configureStore({
  reducer: {
    cart : cart.reducer
  }
}) 