import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Button, Container, Nav, Navbar, Row, Spinner } from 'react-bootstrap';
import { useState, createContext, useEffect, lazy, Suspense } from 'react';
import data from './data.js';
import { Routes, Route, useNavigate, Outlet } from 'react-router-dom'

import axios from 'axios';

import { useQuery } from '@tanstack/react-query';

import Product from './pages/product';
import { useDispatch, useSelector } from 'react-redux';

// import Detail from './pages/detail';
// import Cart from './pages/cart.js'

const Detail = lazy(() => import('./pages/detail.js'));
const Cart = lazy(() => import('./pages/cart.js'));

export let Context1 = createContext();

function App() {

  useEffect(()=>{
    let savedWatched = localStorage.getItem('watched');
    if ( savedWatched === null ) {
      localStorage.setItem('watched', JSON.stringify([]));
    }
    
  }, []);

  let [shoes, setShoes] = useState(data);
  let [pages, setPages] = useState(2);
  let [loading, setLoading] = useState(false);
  let [stock, setStock] = useState([10,11,12])
  let navigate = useNavigate();

  let state = useSelector((state)=>{ return state });
  let dispatch = useDispatch();

  let result = useQuery(['작명'], ()=>
    axios.get('https://codingapple1.github.io/userdata.json')
    .then((a)=>{
      // console.log(a.data)
      // console.log('요청됨')
      return a.data
    })
  )


  
  
  
  
  let 본상품 = JSON.parse(localStorage.getItem('watched'));

  return (
    <div className="App">

      
      <Navbar bg="dark" data-bs-theme="dark">
        <Container>
          <Navbar.Brand onClick={()=>{ navigate('/') }} style={{cursor:'pointer'}}>Leezakaya</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link onClick={()=>{ navigate('/') }}>Home</Nav.Link>
            <Nav.Link onClick={()=>{ navigate('/about') }}>About</Nav.Link>
            <Nav.Link onClick={()=>{ navigate('/detail') }}>Products</Nav.Link>
            <Nav.Link onClick={()=>{ navigate('/event') }}>Events</Nav.Link>
            <Nav.Link onClick={()=>{ navigate('/cart') }}>Cart
            <span class="badge bg-danger nav-badge">{state.cart.length}</span></Nav.Link>
          </Nav>
          <Nav className='ms-auto' style={{'color': 'white'}}>
            { result.isLoading && '로딩중' }
            { result.error && '에러남' }
            { result.data && result.data.name }
            { console.log(result.data)}
          </Nav>
        </Container>
      </Navbar>
      {/* 사이드 바 (오른쪽) 최근 본 상품 */}
      <div className='side-bar'>
        <p>최근본상품</p>
        {
          본상품 === null
          ? null
          : 본상품.map(function(data, i)
          {
            return (
              <img 
              onClick={() => {navigate('/detail/'+ parseInt(data))}}
              className='watched' style={{cursor:'pointer'}}
              src={'https://codingapple1.github.io/shop/shoes'+ (parseInt(data) + 1) + '.jpg'} />
            )
          })
        }
      </div>
      <Suspense fallback={<div>로딩중</div>}>
      <Routes>
        <Route path='/' element={
          <>
          <div className='main-bg'></div>
          <Container>
            <Row>
              
              {
                shoes.map(function(data, i)
                {
                  return (
                    <Product id={i} shoes={shoes}></Product>
                  )
                })
              }

            </Row>
              {
                loading === true
                ? <Spinner animation="border" />
                : null
              }
              {
                pages < 4
                ? <Button onClick={()=>{
                  setLoading(true);
                  axios.get(`https://codingapple1.github.io/shop/data${pages}.json`)
                  .then((result)=>{
                    let copy = [...shoes];
                    // console.log(copy);
                    // console.log(...result.data);
                    copy.push(...result.data);
                    // console.log(copy);
                    setShoes(copy);
                    setPages(pages + 1);
                    setLoading(false);
                  })
                  .catch(()=>{
                    console.log('실패')
                    setLoading(false);
                  })
                }}>더 보기</Button>
                : null
              }
            
          </Container>
          </>
        }/>
        <Route path='/detail/:id' element={
          <>
          <Container className='mt-5'>
            <Context1.Provider value={{shoes, stock}}>
              <Detail shoes={shoes} /> 
            </Context1.Provider>
          </Container> 
          </>
        }/>
        <Route path='/about' element={<About/>}>
          <Route path='member' element={<div>맴버페이지</div>} />
          <Route path='location' element={<div>로케페이지</div>} />
        </Route>
        <Route path='/event' element={<EventPage/>}>
          <Route path='one' element={<p>첫 주문시 양배추즙 서비스</p>}/>
          <Route path='two' element={<p>생일기념 쿠폰 받기</p>}/>
        </Route>
        <Route path='/cart' element={<Cart/>}/>
        <Route path='*' element={<div>없는 페이지 입니다</div>}/>
        
      </Routes>
      </Suspense>
      
      
    </div>
  );
}

function About() {
  return (
    <div>
      <h4>회사 정보 이므니다</h4>
      <Outlet></Outlet>
    </div>
  )
}

function EventPage() {
  return(
    <div>
      <h4>오늘의 이벤트는!</h4>
      <Outlet></Outlet>
    </div>
  )
}



export default App;
